.promo-contacts {
    width: 100%;
    min-height: 614px;
    background:    url(../img/map-marker-svg.svg),
     url(../img/layer-153.jpg);
    background-repeat: no-repeat, no-repeat;
    background-size: 36px 35px,100% 100%;
    background-position: 50% 88%, bottom;
}
.promo-contacts__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    min-height: 395px;
    background-color: #ffffff;
}
.promo-contacts__wrapper::after {
    content: '';
    background: url(../img/triangle-white-footer-svg.svg) no-repeat;
    background-size:cover;
    background-position: center;
    width: 100%;
    height: 61px;
    position: absolute;
    bottom:-47px;
}
.promo-contacts__heading {
    font-family: 'Open Sans', sans-serif;
    font-size: 24px;
    font-weight: 300;
    color:#283645;
    width:70%;
    text-align: center;
    margin-top: 45px;
    margin-bottom: 5px;
}
.contacts-list {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.contacts-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 28px;
}
.contacts-item:last-of-type {
    margin-bottom: 0px;
}
.contacts-item__heading {
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    font-weight: 700;
}
.contacts-item__description {
    font-family: 'Open Sans', sans-serif;
    font-size: 24px;
    font-weight: 300;
    color:#d22856;
    text-align: center;
}
.description-adress {
    background-color: #ffffff;
    width: 212px;
}
@media(min-width:@tablet-width) {
    .promo-contacts {
        min-height: 760px;
        background:    url(../img/map-marker-svg.svg),
        url(../img/layer-153-tablet.jpg);
       background-repeat: no-repeat, no-repeat;
       background-size: 36px 35px,100% 100% ;
       background-position: 50% 79%, 1px 100px;
    }
    .promo-contacts__wrapper::after {
        content: '';
        background: url(../img/triangle-white-footer-svg.svg) no-repeat;
        background-size:100% 100px;
        background-position: center;
        width: 100%;
        height: 100px;
        position: absolute;
        bottom: -84px;
    }
    .promo-contacts__heading {
        font-size: 40px;
    }
    .contacts-list {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
        margin-top: 50px;
        width:87%;
    }
    .contacts-item {
        min-width: 204px;
    }
    .contacts-item__heading {
        margin-bottom: 6px;
    }
    
}