@family {
  font-family: 'Open Sans', 'Arial' sans-serif;
}
body {
  margin: 0;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 0px;
  height: 0px;
  margin: -1px;
  border: 0;
  padding: 0;
  overflow: hidden;
}
.hidden-image {
  display: none;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
p {
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
fieldset {
  padding: 0;
  margin: 0;
  border: none;
}
/*  === Разметка верхней части шапки (лого + меню) === */
.page-header__wrapper {
  background: url(../img/back-mobile-1-x-jpg@3x.png) no-repeat, url(../img/iphone-5-in-hand-png.png) no-repeat;
  background-size: cover;
  width: 100%;
  min-height: 462px;
}
.page-header__top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: nowrap;
  align-items: center;
  min-height: 66px;
  background-color: rgba(0, 0, 0, 0.3);
  overflow: hidden;
  position: relative;
}
.page-header__logo {
  padding-left: 20px;
}
.page-header__logo-image--tablet {
  display: none;
}
.page-header__menu {
  position: relative;
  width: 50px;
  height: 24px;
  background: url('../img/menu-burger-svg.svg');
  background-size: 50px 24px;
  margin-right: 20px;
}
.main-nav {
  top: 67px;
  z-index: 1;
  display: none;
  padding: 0;
  margin: 0;
  list-style: none;
  width: 100%;
}
.nav-list {
  height: 252px;
  width: 100%;
  margin: 0;
  clip: initial;
  position: relative;
  visibility: visible;
}
.nav-list__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-height: 63px;
  background-color: #283645;
  box-shadow: 0 -1px 0 #000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
.nav-list__item:last-child {
  border-bottom: 1px solid #000000;
}
.nav-list__item-link {
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
  line-height: 65px;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
}
/*
    /=== Конец разметки верхней части шапки (лого + меню) ===
*/
/* === Разметка средней части шапки === */
.page-header__top-bottom {
